#clock {
    position: absolute;
    left: 67.75vmin;
    top: .4vmin;
    margin-block-start: 0; margin-block-end: 0; font-size: 2.8vmin;
        overflow: hidden;
    inline-size: 100%;
  
      color: yellow;
    width: 20vmin;
    height: 5.8vmin;
    float: left;
  }
  
  .bbc {
    position: relative;
    left: 2.2%;
    width: 100%;
      height: 9vmin;
  
    background-size: cover;
  }
  
  .yellow {color: #ff0;}
  .green {color: #0f0;}
  .white {color: #fff;}
  .cyan {color: #0ff;}
  .downabit {position: relative; top: 2vmin;}
  .doblue {background-color: blue; padding-top: .5vmin;}
  .red {color: red;}
  .blue {color: blue;}
  .blinking {color: #0f0; animation: blink 1s steps(1, end) infinite;}
  @keyframes blink {
    0% {opacity: 0;}
    50% {opacity: 1;}
  }
  
  Link {
    z-index: 9999;
  }

.mycontainer {font-family: 'modesevenregular';
    background-color: black;
    color: white;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  
  p {	margin-block-start: 0; margin-block-end: 0; font-size: 2.8vmin;
      
      line-height: 1.25;
      color: white;
    overflow-wrap: break-word;
    inline-size: 100%;
  }
  .myd {position: relative; top: -1.7vmin; scale: 1 2;}
  
  @font-face {
      font-family: 'modesevenregular';
      src: url(data:application/font-woff2;charset=utf-8;base64,) format('woff2'),
           url(data:application/font-woff;charset=utf-8;base64,) format('woff');
      font-weight: normal;
